
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'Withdraw',
components: {
XHeader,
Cell,
Group,
Popup,
XInput,
XButton
},
data() {
return {
money: '',
password: '',
withdrawInfo: {}, // 提现信息
checkBank: {}, // 已选的用户银行卡
userBankList: [], // 用户银行卡列表
showUserBank: false, // 显示用户银行卡列表
showAddBank: false, // 显示添加银行卡
bankList: [], // 可添加银行卡列表
addBankInfo: {}, // 要添加的银行卡信息
addBankCard: '', // 要添加的银行卡号
addBankRealName: '', // 添加的真是姓名
showAddBankList: false 
};
},
async created() {
await this.getInfo();
await this.getUserBank();
await this.getBankList();
},
methods: {

async getBankList() {
let res = await this.$http('/queryBankList');
this.bankList = res.returnList;
},

async getUserBank() {
let res = await this.$http('/queryUserBankList');
this.userBankList = res.returnList;
},

async getInfo() {
let res = await this.$http('/queryAccOutCondition');
console.log(res);

if (res.returnMap.isPassword==0) {
this.$vux.toast.show({
text: '提现密码不能为空,请先设置提现密码'
});
this.$router.push('/setMoneyPassword');
return;
}
if (res.returnMap.isPasswordEquals==1) {
this.$vux.toast.show({
text: '登录密码不能和提现密码一样,请先修改提现密码'
});
this.$router.push('/moneyPassword');
return;
}

if (!res.returnMap.isHasRealName) {
this.$vux.toast.show({
type: 'text',
text: '请先完善信息'
});
this.$router.push('/info');
return;
}
this.withdrawInfo = Object.assign({}, res.returnMap);
},

handleBankCheck(it) {
this.checkBank = it;
this.showUserBank = false;
},

handleAddBankList(it) {
this.addBankInfo = Object.assign({}, it);
this.showAddBankList = false;
},

async bindBankCard() {
this.$vux.loading.show({
text: 'Loading'
});
let req = {
bankType: this.addBankInfo.bankType,
bankAccountName: this.addBankRealName,
bankAccount: this.addBankCard
};
let res = await this.$http('/userBindCard', {
body: req
});
this.$vux.loading.hide();
if (res.returnCode === '0000') {
this.$vux.toast.show({
text: '添加成功',
type: 'success'
});
this.showAddBank = false;
this.getUserBank();
} else {
this.$vux.toast.show({
text: res.returnMessage,
type: 'warn'
});
}
},

async toWithdraw() {
if (!this.checkBank.bank || !this.money || !this.password) {
this.$vux.toast.show({
text: '请完善信息',
type: 'warn'
});
if(this.money==null){
this.$router.push("");
}
return;
}
let res = await this.$http('/userAccOut', {
body: {
bankId: this.checkBank.bankId,
outAmount: this.money,
outPassword: this.password
}
});
if (res.returnCode === '0000') {
this.$vux.toast.show({
text: '申请成功',
type: 'success'
});
this.showAddBank = false;
this.$router.go(-1);
} else {
this.$vux.toast.show({
text: res.returnMessage,
type: 'warn'
});
}
}
}
};



